"use client";

import React, { useState } from 'react';
import { Stack, Group, TextInput, Textarea, Button, Alert } from '@mantine/core';
import { IconRobot } from '@tabler/icons-react';

interface KnowledgeFrameworkCreatorProps {
  mode?: 'ai' | 'manual';
  onCancel?: () => void;
  onCreated?: () => void;
}

export const KnowledgeFrameworkCreator: React.FC<KnowledgeFrameworkCreatorProps> = ({ mode, onCancel, onCreated }) => {
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [submitting, setSubmitting] = useState(false);

  const handleSubmit = async () => {
    try {
      setSubmitting(true);
      if (typeof window !== 'undefined' && (window as any).__TAURI__) {
        const { invoke } = await import('@tauri-apps/api/core');
        await invoke('create_knowledge_framework', { framework: { title, description } });
      }
      onCreated?.();
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <Stack gap="md">
      {mode === 'ai' && (
        <Alert color="blue" variant="light" icon={<IconRobot size={16} />}>AI å°†æ ¹æ®è¾“å…¥è‡ªåŠ¨ç”ŸæˆçŸ¥è¯†æ¡†æ¶è‰æ¡ˆ</Alert>
      )}
      <TextInput label="æ ‡é¢˜" placeholder="æ¡†æ¶æ ‡é¢˜" value={title} onChange={(e) => setTitle(e.currentTarget.value)} required />
      <Textarea label="æè¿°" placeholder="ç®€è¦æè¿°æ¡†æ¶" value={description} onChange={(e) => setDescription(e.currentTarget.value)} minRows={3} required />
      <Group justify="flex-end">
        {onCancel && <Button variant="light" onClick={onCancel}>å–æ¶ˆ</Button>}
        <Button onClick={handleSubmit} loading={submitting} disabled={!title || !description}>ä¿å­˜</Button>
      </Group>
    </Stack>
  );
};


